import React, { createContext, useContext } from 'react';
import PropTypes from 'prop-types';
import { useRecipes } from '../hooks/useRecipes';
import { useAuthValue } from '../context/AuthContext';

export const RecipesContext = createContext();

//! doesnt have to be in a context hook
export const RecipesProvider = ({ children }) => {
  const { currentUser } = useAuthValue();
  const { recipes, setRecipes, userId, setUserId } = useRecipes(
    currentUser && currentUser.uid
  );

  return (
    <RecipesContext.Provider value={{ recipes, setRecipes, userId, setUserId }}>
      {children}
    </RecipesContext.Provider>
  );
};

export const useRecipesValue = () => useContext(RecipesContext);

RecipesProvider.propTypes = {
  children: PropTypes.node.isRequired,
};
