import { useState, useEffect } from 'react';
import { firebase } from '../firebase';
import { sortObjectKeys } from '../helpers/sortObjectKeys';

export const useRecipes = (yo = '') => {
  const [recipes, setRecipes] = useState([]);
  const [userId, setUserId] = useState('');

  useEffect(() => {
    firebase
      .firestore()
      .collection('recipes')
      .where('userId', '==', yo)
      .orderBy('name')
      .get()
      .then((snapshot) => {
        const allRecipes = snapshot.docs.map((recipe) => {
          return {
            ...recipe.data(),
            docId: recipe.id,
          };
        });

        console.log('userId', yo);

        const newRecipe = recipes.map((recipe) => sortObjectKeys(recipe));
        const newAllRecipe = allRecipes.map((recipe) => sortObjectKeys(recipe));

        if (JSON.stringify(newRecipe) !== JSON.stringify(newAllRecipe)) {
          setRecipes(allRecipes);
        }
      });
  }, [recipes, yo]);

  return { recipes, setRecipes, userId, setUserId };
};
